// Text alignment
.text-left {
    text-align: left; }

.text-right {
    text-align: right; }

.text-center {
    text-align: center; }

.text-justify {
    text-align: justify; }

// Font weights
.weight-light {
    font-weight: 300; }

.weight-normal {
    font-weight: 400; }

.weight-semibold {
    font-weight: 600; }

// All-caps text
.text-uppercase {
    text-transform: uppercase; }

// Hide text
.screen-reader,
.text-hide {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px; }
